import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';

@Injectable()
export class FirmwareRepositoryNavigationFactory implements NavigatorNodeFactory {
  node: NavigatorNode;

  constructor() {
    this.node = new NavigatorNode({
      label: gettext('Firmware repository'),
      path: 'firmware',
      icon: 'c8y-firmware',
      parent: gettext('Management'),
      priority: 1000
    });
  }

  get(): NavigatorNode {
    return this.node;
  }
}
