import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { IManagedObject, IResultList } from '@c8y/client';
import {
  AlertService,
  GainsightService,
  gettext,
  memoize,
  ModalService,
  Status
} from '@c8y/ngx-components';
import {
  PRODUCT_EXPERIENCE,
  RepositoryService,
  RepositoryType
} from '@c8y/ngx-components/repository/shared';
import { TranslateService } from '@ngx-translate/core';
import { get, isNil, property } from 'lodash-es';
import { BsModalService, ModalOptions } from 'ngx-bootstrap/modal';
import { BehaviorSubject, combineLatest, from, Observable } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  map,
  shareReplay,
  switchMap,
  tap
} from 'rxjs/operators';
import { AddFirmwareModalComponent } from './add-firmware-modal.component';
import { AddFirmwarePatchModalComponent } from './add-firmware-patch-modal.component';

@Component({
  selector: 'c8y-firmware-list',
  templateUrl: 'firmware-list.component.html'
})
export class FirmwareListComponent {
  textFilter$: BehaviorSubject<string> = new BehaviorSubject(null);
  reload$: BehaviorSubject<void> = new BehaviorSubject(null);
  reloading = false;
  firmwares$: Observable<IResultList<IManagedObject>> = combineLatest([
    this.textFilter$.pipe(
      debounceTime(400),
      distinctUntilChanged(),
      tap(this.sendListFilteredEvent.bind(this)),
      map(text => text ?? '')
    ),
    this.reload$
  ]).pipe(
    tap(() => {
      this.reloading = true;
    }),
    switchMap(([text]) => this.getFirmwares(text)),
    tap(() => {
      this.reloading = false;
    }),
    shareReplay(1)
  );
  canAddFirmwarePatch$: Observable<boolean> = from(
    this.repositoryService.listRepositoryEntries(RepositoryType.FIRMWARE, { skipLegacy: true })
  ).pipe(map(({ data }) => data.length > 0));
  isLegacy = this.repositoryService.isLegacyEntry.bind(this.repositoryService);
  readonly DEVICE_TYPE_NOT_AVAILABLE: string = gettext('No device type available');

  constructor(
    private repositoryService: RepositoryService,
    private modalService: ModalService,
    private bsModalService: BsModalService,
    private translateService: TranslateService,
    private alertService: AlertService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private gainsightService: GainsightService
  ) {}

  getFirmwares(partialText?: string) {
    const properties: string[] = ['name', 'description', 'c8y_Filter.type'];
    const partialTextFilter = { partialText, properties };
    return this.repositoryService.listRepositoryEntries(RepositoryType.FIRMWARE, {
      partialTextFilter
    });
  }

  addFirmware() {
    const config: ModalOptions<AddFirmwareModalComponent> = {
      class: 'modal-sm',
      ariaDescribedby: 'addFirmwareModalDescription',
      ariaLabelledBy: 'addFirmwareModalTitle',
      ignoreBackdropClick: true
    };
    const modalRef = this.bsModalService.show(AddFirmwareModalComponent, config);
    modalRef.content.saved.subscribe(savedFirmware => this.editFirmware(savedFirmware));
  }

  addFirmwarePatch() {
    const config: ModalOptions<AddFirmwarePatchModalComponent> = {
      class: 'modal-sm',
      ariaDescribedby: 'addFirmwarePatchModalDescription',
      ariaLabelledBy: 'addFirmwarePatchModalTitle',
      ignoreBackdropClick: true
    };
    const modalRef = this.bsModalService.show(AddFirmwarePatchModalComponent, config);
    modalRef.content.saved.subscribe(savedFirmware => this.editFirmware(savedFirmware));
  }

  editFirmware(firmware: Partial<IManagedObject>) {
    this.router.navigate([firmware.id], { relativeTo: this.activatedRoute });
  }

  async deleteFirmware(firmware: IManagedObject) {
    try {
      const title = gettext('Delete firmware');
      const body = `
        ${this.translateService.instant(
          gettext(
            'You are about to delete firmware "{{ name }}" with all its versions and patches.'
          ),
          { name: firmware.name }
        )}
        ${this.translateService.instant(gettext('This operation is irreversible.'))}
        ${this.translateService.instant(gettext('Do you want to proceed?'))}
      `;
      const labels = {
        ok: gettext('Delete')
      };
      await this.modalService.confirm(
        title,
        body,
        Status.DANGER,
        labels,
        {},
        { eventName: PRODUCT_EXPERIENCE.FIRMWARE.EVENTS.REPOSITORY }
      );
      await this.repositoryService.delete(firmware);
      this.alertService.success(gettext('Firmware deleted.'));
      this.reload$.next();
    } catch (ex) {
      // only if not cancel from modal
      if (ex) {
        this.alertService.addServerFailure(ex);
      }
    }
  }

  @memoize(property('id'))
  getBaseVersionsCount$(firmware: IManagedObject): Observable<number | string> {
    return this.firmwares$.pipe(
      switchMap(() => this.repositoryService.getBaseVersionsCount$(firmware)),
      shareReplay(1)
    );
  }

  getDeviceTypeTitle(firmware: IManagedObject): string {
    return get(
      firmware,
      'c8y_Filter.type',
      this.translateService.instant(this.DEVICE_TYPE_NOT_AVAILABLE)
    );
  }

  private sendListFilteredEvent(filterQuery: string) {
    if (!isNil(filterQuery)) {
      this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.FIRMWARE.EVENTS.REPOSITORY, {
        component: PRODUCT_EXPERIENCE.FIRMWARE.COMPONENTS.DEVICE_FIRMWARE_LIST,
        action: PRODUCT_EXPERIENCE.FIRMWARE.ACTIONS.FILTER_FIRMWARE_LIST,
        filterQuery
      });
    }
  }
}
