import { Component, EventEmitter, Output, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { IManagedObject } from '@c8y/client';
import { AlertService, gettext, PickedFiles } from '@c8y/ngx-components';
import {
  ModalModel,
  PRODUCT_EXPERIENCE,
  RepositoryCategory,
  RepositoryService,
  RepositoryType
} from '@c8y/ngx-components/repository/shared';
import { assign, get, isUndefined } from 'lodash-es';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { BehaviorSubject, from, Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged, switchMap, tap } from 'rxjs/operators';

@Component({
  selector: 'c8y-add-firmware-software-modal',
  templateUrl: 'add-firmware-modal.component.html'
})
export class AddFirmwareModalComponent {
  PRODUCT_EXPERIENCE = PRODUCT_EXPERIENCE;
  @ViewChild('firmwareForm', { static: false }) form: NgForm;
  @Output() saved: EventEmitter<RepositoryCategory> = new EventEmitter<RepositoryCategory>();
  onInput: BehaviorSubject<string> = new BehaviorSubject<string>('');
  model: ModalModel = {
    selected: undefined,
    version: undefined,
    description: undefined,
    deviceType: undefined,
    binary: {
      file: undefined,
      url: undefined
    }
  };
  firmwaresResult;
  saving = false;
  firmwarePreselected = false;
  textForFirmwareUrlPopover: string =
    gettext(`Path for binaries can vary depending on device agent implementation, for example:
    /firmware/binaries/firmware1.bin
    https://firmware/binary/123
    ftp://firmware/binary/123.tar.gz
  `);
  private inputSubscription$: Subscription;

  constructor(
    private modal: BsModalRef,
    private repositoryService: RepositoryService,
    private alert: AlertService
  ) {}

  ngOnInit() {
    this.setInitialState();
    this.loadFirmwares();
  }

  setInitialState() {
    if (this.model.selected) {
      this.firmwarePreselected = true;
    }
  }

  loadFirmwares() {
    this.inputSubscription$ = this.onInput
      .pipe(
        tap(() => {
          if (!this.firmwarePreselected) {
            this.model.description = null;
            if (this.form) {
              this.form.form.get('description').reset();
            }
          }
        }),
        debounceTime(300),
        distinctUntilChanged(),
        switchMap(searchStr => this.getFirmwareResult(searchStr))
      )
      .subscribe(result => {
        this.firmwaresResult = result;
      });
  }

  getFirmwareResult(searchStr: string) {
    return from(
      this.repositoryService.listRepositoryEntries(RepositoryType.FIRMWARE, {
        partialName: searchStr,
        skipLegacy: true
      })
    );
  }

  async save() {
    this.saving = true;
    this.repositoryService
      .create(this.model, RepositoryType.FIRMWARE)
      .then(savedFirmware => {
        this.successMsg();
        this.saving = false;
        this.saved.next(savedFirmware);
        this.cancel();
      })
      .catch(e => {
        this.saving = false;
        this.saved.error(e);
        this.cancel();
      });
  }

  successMsg() {
    const msg = gettext('Firmware added.');
    this.alert.success(msg);
  }

  cancel() {
    this.modal.hide();
    this.saved.complete();
  }

  ngOnDestroy() {
    this.inputSubscription$.unsubscribe();
  }

  onFile(dropped: PickedFiles) {
    if (!isUndefined(dropped.url)) {
      this.model.binary = {
        url: dropped.url
      };
      return;
    } else if (dropped.droppedFiles) {
      this.model.binary = {
        file: dropped.droppedFiles[0].file
      };
      return;
    } else {
      this.model.binary = {
        file: undefined,
        url: undefined
      };
    }
  }

  onFirmwareSelect(firmware: IManagedObject) {
    assign(this.model, {
      selected: firmware,
      description: firmware.description,
      deviceType: get(firmware, 'c8y_Filter.type')
    });
  }
}
